import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			isClicked: false,
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			productData: {},
			choose_number:1,
			addressData: {

			},
			totalFee:0
		}
	},
	onLoad(option) {
		//商品数据
		//let data = JSON.parse(option.data);
		console.log(option);
		var _this = this;
		uni.showLoading({
			'title':'加载中...'
		})
		this.$http.post('/api/goods/index', option).then(res => {
			if (res.data.code != 1) {
				uni.showToast({
					title: res.data.info,
					icon: 'none'
				});
			} else {
				var data = res.data.data
				_this.addressData = data.addressData
				_this.productData = data.data
				_this.productData.attr = data.attr
				_this.totalFee = this.productData.attr.price_selling;
				uni.hideLoading()
			}
		}).catch(err => {
			console.log(err, 333);
		})
		// this.getPageData(data);
		uni.hideShareMenu()
	},
	methods: {
		chooseNumber(value){
			this.choose_number = value;
			this.calculateTotol()
		},
		calculateTotol(){
			if(this.productData.attr){
				console.log(221)
				let price = this.productData.attr != undefined ? this.productData.attr.price_selling:0;
				this.totalFee = (price*1*this.choose_number).toFixed(2);
			}
		},
		getPageData(option) {
			console.log(option)
		},
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit() {
			// console.log(e)
			var _this = this;
			_this.doCommit()
			// uni.requestSubscribeMessage({
			// 	tmplIds: ['-ib8s8PRjp81McgQ6WJ0EGtHJn-nJ7lZuByZZv9nI-A'],
			// 	success(res) {

			// 	},
			// 	fail(err) {
			// 		console.log(err)
			// 		_this.doCommit();
			// 	}
			// })
		},
		doCommit() {
			var _this = this;
			let data = {
				'productData': _this.productData,
				'number':_this.choose_number,
				// 'form_id':e.detail.formId != undefined ? e.detail.formId : ''
			};
			if (_this.addressData != undefined) {
				data.express_address_id = _this.addressData.id
			} else {
				uni.showToast({
					title: '请添加收货地址！',
					icon: 'none'
				});
				return false;
			}

			if (_this.isClicked == true) {
				uni.showToast({
					title: '请不要重复提交！',
					icon: 'none'
				});
				return false;
			}
			_this.isClicked = true;
			_this.$http.post('/store/api.member.order/set', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var orderId = res.data.data
					uni.showToast({
						title: res.data.info,
						icon: 'success'
					});
					setTimeout(function() {
						uni.redirectTo({
							url: '/pages/money/goods_pay?orderId=' + orderId
						})
					}, 1000)
				}
			}).catch(err => {
				console.log(err);
			})
		},
		stopPrevent() {}
	}
}
